import gql from 'graphql-tag'

import { Api } from '../api'
import { UnpersistedOrganism, Organism } from '../../types'

export const upsertOrganismMutation = gql`
  mutation (
    $sampleId: Int!
    $taxonId: Int!
    $lifeStageId: Int!
    $splitCount: Int!
    $bigRareCount: Int
    $comments: String
    $noteTypeIds: [Int]
  ) {
    createSampleTaxaInfo(
      sampleId: $sampleId
      taxonomyId: $taxonId
      lifeStageId: $lifeStageId
      splitCount: $splitCount
      comments: $comments
      noteTypeIds: $noteTypeIds
      bigRareCount: $bigRareCount
    )
  }
`

export const upsertOrganism =
  (api: Api) =>
  async (organism: UnpersistedOrganism | Organism): Promise<{ id: number }> => {
    const data = await api.mutation<{
      createSampleTaxaInfo: number
    }>(upsertOrganismMutation, { lifeStageId: -1, ...organism })
    const { createSampleTaxaInfo: id } = data

    return { id }
  }
