/** @jsxImportSource @emotion/react */
import React, { useState } from 'react'
import TextField from '@mui/material/TextField'
import Autocomplete from '@mui/material/Autocomplete'
import ListItem from '@mui/material/ListItem'
import Button, { ButtonProps } from '@mui/material/Button'
import KeyboardReturnIcon from '@mui/icons-material/KeyboardReturn'
import AddIcon from '@mui/icons-material/AddCircleOutline'
import FilterAltIcon from '@mui/icons-material/FilterAlt'
import parse from 'autosuggest-highlight/parse'
import match from 'autosuggest-highlight/match'

import { Taxon } from '../../types'
import { Badge } from '../Badge'

const MAX_MATCHES = 100
const MAX_DISPLAYED_SYNONYMS = 10

type TaxaOptionStatus = 'inSample' | 'notInSample'

export interface PresentableTaxonOption extends Taxon {
  status: TaxaOptionStatus
  canAdd: boolean
  canFilter: boolean
  defaultAction?: 'add' | 'filter'
}

export interface TaxonomyFieldProps {
  options: PresentableTaxonOption[]
  onAddOrganism: (taxon: PresentableTaxonOption) => void
  onAddFilter: (taxon: PresentableTaxonOption) => void
  inputRef?: React.RefObject<HTMLInputElement>
}

interface OptionProps extends React.HTMLAttributes<HTMLLIElement> {
  taxon: PresentableTaxonOption
  inputValue: string
  onAddOrganism: (taxon: PresentableTaxonOption) => void
  onAddFilter: (taxon: PresentableTaxonOption) => void
}

const HighlightedText = ({ text, highlightText }: { text: string; highlightText: string }): JSX.Element => (
  <>
    {parse(text, match(text, highlightText)).map<JSX.Element>(
      // TODO: match is not finding all occurrences, but this needs to be rewritten custom anyway because the algoritm isn't exactly what we need
      (part: { text: string; highlight: boolean }, index: number) => (
        <span
          key={index}
          style={{
            fontWeight: part.highlight ? 700 : 400,
          }}
        >
          {part.text}
        </span>
      )
    )}
  </>
)

const ActionButton = ({
  isDefault,
  children,
  onClick,
  className,
  disabled,
  ...restProps
}: ButtonProps & {
  isDefault?: boolean
}) => {
  return (
    <div css={{ position: 'relative' }} className={className}>
      {disabled && (
        <div
          css={{ width: '100%', height: '100%', position: 'absolute', cursor: 'default' }}
          onClick={(e) => {
            // this element is a bit of a hack to use a disabled button but still
            // catch the click to prevent autocomplete from choosing row
            e.stopPropagation()
          }}
        />
      )}
      <Button variant="outlined" size="small" onClick={onClick} disabled={disabled} data-skip-choose {...restProps}>
        {children}
        {isDefault && <KeyboardReturnIcon sx={{ marginLeft: '0.25em', fontSize: '100%' }} />}
      </Button>
    </div>
  )
}

const Option = ({ taxon, inputValue, onAddFilter, onAddOrganism, ...restProps }: OptionProps) => {
  const { name, levelName, synonyms, canAdd, canFilter, defaultAction } = taxon

  const synonymArea = [] as JSX.Element[]

  if (synonyms.length <= MAX_DISPLAYED_SYNONYMS) {
    synonymArea.push(<HighlightedText key="only" text={synonyms.join(', ')} highlightText={inputValue} />)
  } else {
    const isSynonymsMatched = match(synonyms.join(' '), inputValue).length > 0

    if (isSynonymsMatched) {
      synonyms.forEach((synonym) => {
        if (match(synonym, inputValue).length === 0) return
        if (synonymArea.length > 0) synonymArea.push(<span key={`${synonym} separator`}>, </span>)
        synonymArea.push(<HighlightedText key={synonym} text={synonym} highlightText={inputValue} />)
      })
      synonymArea.push(
        <span key="remainder" css={{ opacity: 0.75 }}>
          {' '}
          ({synonyms.length - synonymArea.length} unmatched not shown)
        </span>
      )
    } else {
      synonymArea.push(
        <React.Fragment key="more">
          {synonyms.slice(0, MAX_DISPLAYED_SYNONYMS).join(', ')}
          <span css={{ opacity: 0.75 }}> ({synonyms.length - MAX_DISPLAYED_SYNONYMS} more not shown)</span>
        </React.Fragment>
      )
    }
  }

  return (
    <ListItem
      {...restProps}
      css={{
        borderBottom: '1px solid lightgrey',
        display: 'flex',
      }}
    >
      <span css={{ maxWidth: '15em', flex: 1 }}>
        <HighlightedText text={name} highlightText={inputValue} />
        {taxon.isTargetEffort && (
          <Badge
            badgeColor="#bbbbbb"
            tooltip="Taxon is at for below the SAFIT II required target effort"
            css={{ marginLeft: '0.5em' }}
            label="S"
          />
        )}
      </span>
      <span css={{ maxWidth: '6em', flex: 1 }}>{levelName}</span>
      <span css={{ fontStyle: 'italic', flex: 1 }}>{synonymArea}</span>
      <div css={{ display: 'grid', gridGap: '0.25em', gridTemplateColumns: 'auto auto' }}>
        <ActionButton
          onClick={() => onAddOrganism(taxon)}
          isDefault={defaultAction === 'add'}
          disabled={!canAdd}
          startIcon={<AddIcon />}
        >
          Add Organism
        </ActionButton>
        <ActionButton
          onClick={() => onAddFilter(taxon)}
          isDefault={defaultAction === 'filter'}
          disabled={!canFilter}
          startIcon={<FilterAltIcon />}
        >
          Add Filter
        </ActionButton>
      </div>
    </ListItem>
  )
}

const Group = ({ group, children }: { group: TaxaOptionStatus | string; children?: React.ReactNode }) => {
  // TODO: group is always TaxaOptionStatus and I don't want to allow any group as string, but TS chokes without allowing string
  const color = group === 'inSample' ? 'green' : 'blue'
  return (
    <div>
      <div
        css={{
          borderBottom: `3px solid ${color}`,
          color,
          height: '2em',
          display: 'flex',
          alignItems: 'center',
          paddingLeft: '1em',
        }}
      >
        {group === 'inSample' ? 'Already In Sample' : 'Not In Sample'}
      </div>
      {children}
    </div>
  )
}

const filterOptions = (input: PresentableTaxonOption[], { inputValue }: { inputValue: string }) =>
  input
    .filter(({ name, synonyms }) => {
      const filterableName = [name, ...synonyms].join(' ')
      return match(filterableName, inputValue).length > 0
    })
    .slice(0, MAX_MATCHES)

export const TaxonomyField = ({ options, onAddOrganism, onAddFilter, inputRef }: TaxonomyFieldProps): JSX.Element => {
  const [text, setText] = useState('')

  const handleTextChange = (e: React.SyntheticEvent, newValue: string) => {
    if (newValue === '/') return // for slicker keyboard shortcut behavior
    setText(newValue)
  }

  const handleChoose = (
    e: React.SyntheticEvent & { target: { attributes?: { 'data-skip-choose'?: string } } },
    taxon?: PresentableTaxonOption | null
  ) => {
    if (taxon && !e.target.attributes?.['data-skip-choose']) {
      switch (taxon?.defaultAction) {
        case 'add':
          onAddOrganism(taxon)
          break
        case 'filter':
          onAddFilter(taxon)
          break
      }
    }
    window.setTimeout(() => {
      setText('')
    }, 0)
  }

  const handleKeyDown = ({ key }: { key: string }) => {
    if (key !== 'Escape' || !inputRef?.current) return
    inputRef.current.blur()
  }

  const isOptionsPresented = text.length >= 3

  return (
    <Autocomplete
      disablePortal
      options={isOptionsPresented ? options : []}
      onChange={handleChoose}
      renderInput={(params) => <TextField {...params} inputRef={inputRef} label="Taxonomy" />}
      onKeyDown={handleKeyDown}
      renderOption={(optionProps, taxon, { inputValue }) => (
        <Option
          {...optionProps}
          key={taxon.id}
          taxon={taxon}
          inputValue={inputValue}
          onAddOrganism={onAddOrganism}
          onAddFilter={onAddFilter}
        />
      )}
      inputValue={text}
      onInputChange={handleTextChange}
      clearOnEscape
      autoHighlight
      noOptionsText={isOptionsPresented ? 'No taxa found' : '...'}
      groupBy={(taxon) => taxon.status}
      value={null}
      renderGroup={(groupProps) => <Group {...groupProps} />}
      getOptionLabel={({ id }) => String(id)} // being used by Autocomplete for array key; not displayed due to renderOption
      filterOptions={filterOptions}
    />
  )
}
