import gql from 'graphql-tag'

import { Api } from '../api'

interface NewOrganization {
  name: string
  typeId: number
  address1: string
  address2: string
  city: string
  stateId: number
  countryId: number
  zipCode: string
  phone: string
}

export const createOrganizationMutation = gql`
  mutation (
    $name: String!
    $typeId: Int!
    $address1: String!
    $address2: String!
    $city: String!
    $stateId: Int!
    $countryId: Int!
    $zipCode: String!
    $phone: String!
  ) {
    createOrganization(
      organizationName: $name
      organizationTypeId: $typeId
      abbreviation: ""
      address1: $address1
      address2: $address2
      city: $city
      stateId: $stateId
      countryId: $countryId
      zipCode: $zipCode
      phone: $phone
    )
  }
`

export const createOrganization =
  (api: Api) =>
  async (organization: NewOrganization): Promise<number | null> => {
    try {
      const data = await api.mutation<{
        createOrganization: number | null // NOTE: new organization ID
      }>(createOrganizationMutation, { ...organization })

      return data.createOrganization
    } catch (error) {
      return null
    }
  }
