import gql from 'graphql-tag'
import { BoxInfo as GraphBoxInfo } from '@namcbugdb/common'

import { Api } from '../api'
import { Submission } from '../../types'

export const fetchLatestSubmissionByBoxIdQuery1 = gql`
  query ($boxId: Int!) {
    boxInfo(boxId: $boxId) {
      customerName
      latestSubmission {
        versionId
        submissionId
        submitterId
        createdDate
        updatedDate
      }
    }
  }
`

export const fetchLatestSubmissionByBoxIdQuery2 = gql`
  query ($submissionId: Int!) {
    submissionData(submissionId: $submissionId)
    # TODO: need to query userInfo using $submitterId here
  }
`

export const fetchLatestSubmissionByBoxId =
  (api: Api) =>
  async (boxId: number): Promise<Submission | null> => {
    const data1 = await api.query<{ boxInfo: GraphBoxInfo }>(fetchLatestSubmissionByBoxIdQuery1, { boxId })

    const { customerName: organizationName, latestSubmission } = data1.boxInfo
    if (!latestSubmission) return null
    const { versionId: version, submissionId, submitterId, createdDate, updatedDate } = latestSubmission

    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    const data2 = await api.query<{ submissionData: any }>(fetchLatestSubmissionByBoxIdQuery2, {
      submissionId,
      submitterId,
    })

    try {
      const { submissionData } = data2

      // TODO: get from userInfo
      const [firstName, lastName, submitterEmail, submitterPhone] = [
        'Mock',
        'Submitter',
        'submitter@organization.com',
        '123-456-7890',
      ]
      const submitterName = `${firstName} ${lastName}`

      if (typeof submissionData !== 'object') throw new Error()
      const { templateVersion, appVersion, samples } = submissionData
      if (typeof templateVersion !== 'number') throw new Error()
      if (typeof appVersion !== 'string') throw new Error()
      if (!Array.isArray(samples)) throw new Error()

      return {
        id: submissionId,
        boxId,
        organizationName,
        submitterName,
        submitterEmail,
        submitterPhone,
        version,
        submitterId,
        createdDate,
        updatedDate,
        templateVersion,
        appVersion,
        samples,
      }
    } catch (err) {
      throw new Error('Could not parse submissionData')
    }
  }
