import { api } from '@namcbugdb/common'
import { ASTNode } from 'graphql'
import { UseHostedReturn } from '@namcbugdb/react-common'
import log from 'loglevel'

import { endpoints } from '../../config'
import { Api } from './Api'

const TIMEOUT_SECONDS = 30

class GraphError extends Error {
  constructor(message) {
    super()
    this.message = message
  }

  toString() {
    return this.message
  }
}

export class GraphApi implements Api {
  auth: UseHostedReturn
  signOut: () => void

  constructor(auth: UseHostedReturn, signOut: () => void) {
    this.auth = auth
    this.signOut = signOut
  }

  query = async <T>(query: ASTNode, variables?: { [key: string]: unknown }): Promise<T> => {
    const jwt = await this.auth.getCurrentToken()
    const response = await api.apiCall<T>(endpoints.graphql as string, jwt, query, variables, TIMEOUT_SECONDS * 1000)
    const { data, errors } = response
    if (errors || !data) {
      log.error(errors)
      throw new GraphError(errors?.map((error) => error.message).join(';'))
    }
    return data
  }

  queryAllPages = async <QueryT>(
    query: ASTNode,
    variables: { [key: string]: unknown },
    { dataProperty }: { dataProperty: string }
  ): Promise<QueryT> => {
    let offset: number | null | undefined = 0
    // eslint-disable-next-line @typescript-eslint/no-explicit-any
    const allRecords: any = [] // type is the type of record

    while (offset !== null) {
      const lastData = await this.query<QueryT>(query, { ...variables, offset })
      const { records, nextOffset } = lastData[dataProperty]
      if (nextOffset === undefined)
        throw new GraphError('queryAllPages was called without the query returning nextOffset.')
      allRecords.push(...records)
      offset = nextOffset
      if (!offset) return { ...lastData, [dataProperty]: { records: allRecords } }
    }

    throw new Error() // unreachable code; this is just to satisfy TS
  }

  mutation = this.query
}
