import gql from 'graphql-tag'

import { Api } from '../api'

export const stopSortMutation = gql`
  mutation ($sampleId: Int!) {
    labStopSort(sampleId: $sampleId)
  }
`

export const stopSort =
  (api: Api) =>
  async ({ sampleId }: { sampleId: number }): Promise<boolean> => {
    const data = await api.mutation<{
      labStopSort: number // NOTE this is records affected, 1 = success, 0 = failure
    }>(stopSortMutation, { sampleId })

    return !!data.labStopSort
  }
