/** @jsxImportSource @emotion/react */
import React, { Suspense } from 'react'
import TextField from '@mui/material/TextField'
import MuiAutocomplete from '@mui/material/Autocomplete'
import { RecoilValue, useRecoilValue, useRecoilValueLoadable } from 'recoil'
import Skeleton from '@mui/material/Skeleton'

import { ReadOnlyField } from '../ReadOnlyField'
import { CommonFieldProps } from '../../types'

export interface Option {
  value: string | number
  label: string
  disabled?: boolean
}

interface EditableAutocompleteFieldProps extends CommonFieldProps<string | number | null> {
  isClearable?: boolean
  options: Option[]
  optionsSelector?: never
  valueLabel?: never
}

interface SuspensedEditableAutocompleteFieldProps extends CommonFieldProps<string | number | null> {
  options?: never
  optionsSelector: RecoilValue<Option[]>
  valueLabel: string | null
}

export type AutocompleteFieldProps = CommonFieldProps<string | number | null> & {
  isClearable?: boolean
  label?: string
} & (EditableAutocompleteFieldProps | SuspensedEditableAutocompleteFieldProps)

const EditableAutocompleteField = ({
  // CommonFieldProps
  className,
  disabled,
  error,
  gridArea,
  label,
  onChange,
  value,

  // EditableAutocompleteFieldProps
  isClearable,
  options,
}: EditableAutocompleteFieldProps): JSX.Element => {
  const foundValue = options.find((option) => option.value === value) || null

  return (
    <MuiAutocomplete
      className={className}
      disablePortal
      options={options}
      disabled={disabled}
      getOptionLabel={(option) => option.label}
      getOptionDisabled={(option) => !!option.disabled}
      renderInput={(params) => (
        <TextField {...params} label={label} error={error} InputLabelProps={{ shrink: !!foundValue }} />
      )}
      onChange={(event, selected) => {
        onChange(selected && selected.value)
      }}
      size="small"
      value={foundValue}
      css={{ gridArea }}
      disableClearable={!isClearable}
    />
  )
}

const SuspensedEditableAutocompleteField = ({
  optionsSelector,
  // eslint-disable-next-line @typescript-eslint/no-unused-vars
  valueLabel, // deliberately discarded
  ...rest
}: SuspensedEditableAutocompleteFieldProps) => {
  const options = useRecoilValue(optionsSelector)
  return <EditableAutocompleteField options={options} {...rest} />
}

export const AutocompleteField = ({
  options,
  optionsSelector,
  readOnly,
  valueLabel,
  ...restProps
}: AutocompleteFieldProps): JSX.Element => {
  if (readOnly) {
    if (options) {
      const foundValueLabel = options.find((option) => option.value === restProps.value)?.label ?? ''
      return <ReadOnlyField {...restProps} value={foundValueLabel} />
    } else if (optionsSelector) {
      const optionsLoadable = useRecoilValueLoadable(optionsSelector)
      const displayLabel =
        optionsLoadable.state === 'hasValue'
          ? optionsLoadable.contents.find((option) => option.value === restProps.value)?.label ?? ''
          : valueLabel
      return <ReadOnlyField {...restProps} value={displayLabel} />
    }
    throw new Error('AutocompleteField requires either `options` or `optionsSelector`.') // should never be reached
  }

  if (options) return <EditableAutocompleteField options={options} {...restProps} />

  if (optionsSelector && typeof valueLabel !== 'undefined')
    return (
      <Suspense fallback={<ReadOnlyField {...restProps} value={<Skeleton />} />}>
        <SuspensedEditableAutocompleteField optionsSelector={optionsSelector} valueLabel={valueLabel} {...restProps} />
      </Suspense>
    )

  throw new Error('AutocompleteField requires either `options` or `optionsSelector`.') // should never be reached
}
