import React, { Suspense } from 'react'
import { useRecoilValue } from 'recoil'

import { selectSample } from '../../data/selectors'
import { SampleOverview as Component } from './SampleOverview'
import { Fallback } from './Fallback'

export interface SampleOverviewProps {
  sampleId: string | number
}

const Connector = ({ sampleId }: SampleOverviewProps): JSX.Element => {
  const sample = useRecoilValue(selectSample({ id: Number(sampleId) }))

  if (sample === null) return <h2>Sample {sampleId} not found.</h2>

  return <Component sample={sample} />
}

export const SampleOverview = (props: SampleOverviewProps): JSX.Element => (
  <Suspense fallback={<Fallback />}>
    <Connector {...props} />
  </Suspense>
)
