import React, { Suspense, useMemo } from 'react'
import { useRecoilValue } from 'recoil'
import { uniqueId } from 'lodash'

import { selectSample, selectSorterUsers, selectMyUser } from '../../data/selectors'
import { SampleSorting as Component } from './SampleSorting'
import { Fallback } from './Fallback'

export interface SampleSortingProps {
  sampleId: string | number
}

const Connector = ({ sampleId, nonce }: SampleSortingProps & { nonce: string }): JSX.Element => {
  const sorterUsers = useRecoilValue(selectSorterUsers)
  const user = useRecoilValue(selectMyUser)
  const sample = useRecoilValue(selectSample({ id: Number(sampleId), nonce }))

  if (sample === null) return <h2>Sample {sampleId} not found.</h2>

  return <Component sorterUsers={sorterUsers} sample={sample} user={user} />
}

export const SampleSorting = (props: SampleSortingProps): JSX.Element => {
  const nonce = useMemo(uniqueId, [])
  return (
    <Suspense fallback={<Fallback />}>
      <Connector {...props} nonce={nonce} />
    </Suspense>
  )
}
