import { useSetRecoilState } from 'recoil'
import { uniqueId } from 'lodash'

import { notificationsState, AppNotification } from './state'

interface Notifications {
  create: (notification: Omit<AppNotification, 'id'>) => void
}

export const useNotifications = (): Notifications => {
  const setNotifications = useSetRecoilState(notificationsState)

  return {
    create: (notification: Omit<AppNotification, 'id'>) => {
      setNotifications([
        {
          id: uniqueId(),
          type: 'info',
          ...notification,
        },
      ])
    },
  }
}
