import React, { Suspense } from 'react'
import { useRecoilValue } from 'recoil'
import { GrantAction, NAMCResource } from '@namcbugdb/common'

import { selectBoxes, selectUserBoxes, selectBoxStates, selectMyUser } from '../../data/selectors'
import { PrivilegedBoxesListing } from './PrivilegedBoxesListing'
import { CustomerBoxesListing } from './CustomerBoxesListing'
import { Fallback } from './Fallback'

const CustomerConnector = (): JSX.Element => {
  const boxes = useRecoilValue(selectUserBoxes)
  const boxStates = useRecoilValue(selectBoxStates)

  return <CustomerBoxesListing boxes={boxes} boxStates={boxStates} />
}

const PrivilegedConnector = (): JSX.Element => {
  const boxes = useRecoilValue(selectBoxes)
  const boxStates = useRecoilValue(selectBoxStates)

  return <PrivilegedBoxesListing boxes={boxes} boxStates={boxStates} />
}

const Connector = (): JSX.Element => {
  const user = useRecoilValue(selectMyUser)

  if (user.hasPermission(NAMCResource.boxes, GrantAction.readAny)) return <PrivilegedConnector />
  return <CustomerConnector />
}

export const BoxesListing = (): JSX.Element => {
  return (
    <Suspense fallback={<Fallback />}>
      <Connector />
    </Suspense>
  )
}
