import gql from 'graphql-tag'
import { PaginatedRecords, Sample as GraphSample } from '@namcbugdb/common'

import { Api } from '../api'
import { BoxSample } from '../../types'

export const fetchBoxSamplesByBoxIdQuery = gql`
  query ($boxId: Int!) {
    samples(limit: 1000, offset: 0, boxIds: [$boxId]) {
      records {
        sampleId
        sampleDate
        usState
        sampleType
        sampleMethod
        sorterName
        sortStartDate
        sortEndDate
        idErName
        idStartDate
        idEndDate
        customerSiteCode
        visitId
        siteName
      }
      nextOffset
    }
  }
`

export const fetchBoxSamplesByBoxId =
  (api: Api) =>
  async (boxId: number): Promise<BoxSample[]> => {
    const data = await api.queryAllPages<{
      samples: PaginatedRecords<GraphSample>
    }>(fetchBoxSamplesByBoxIdQuery, { boxId }, { dataProperty: 'samples' })

    return data.samples.records.map((sample) => {
      const {
        sampleId: id,
        sampleType: type,
        sampleMethod: method,
        idErName: identifierName,
        idStartDate: identificationStartDate,
        idEndDate: identificationEndDate,
        ...restSample
      } = sample

      return {
        ...restSample,
        id,
        type,
        method,
        identifierName,
        identificationStartDate,
        identificationEndDate,
      }
    })
  }
