import React, { Suspense, useMemo } from 'react'
import { useRecoilValue } from 'recoil'
import { uniqueId } from 'lodash'

import { selectTaxa, selectLifeStages, selectNoteTypes, selectSample, selectMyUser } from '../../data/selectors'
import { SampleTaxonomyIdentification as Component } from './SampleTaxonomyIdentification'
import { Fallback } from './Fallback'

interface SampleTaxonomyIdentificationProps {
  sampleId: string | number
}

const Connector = ({ sampleId, nonce }: SampleTaxonomyIdentificationProps & { nonce: string }): JSX.Element => {
  const taxa = useRecoilValue(selectTaxa)
  const lifeStages = useRecoilValue(selectLifeStages)
  const noteTypes = useRecoilValue(selectNoteTypes)
  const user = useRecoilValue(selectMyUser)
  const sample = useRecoilValue(selectSample({ id: Number(sampleId), nonce }))

  if (sample === null) return <h2>Sample {sampleId} not found.</h2>

  return <Component taxa={taxa} lifeStages={lifeStages} noteTypes={noteTypes} sample={sample} user={user} />
}

export const SampleTaxonomyIdentification = (props: SampleTaxonomyIdentificationProps): JSX.Element => {
  const nonce = useMemo(uniqueId, [])
  return (
    <Suspense fallback={<Fallback />}>
      <Connector {...props} nonce={nonce} />
    </Suspense>
  )
}
