import gql from 'graphql-tag'

import { Api } from '../api'

export const startSortMutation = gql`
  mutation ($sampleId: Int!) {
    labStartSort(sampleId: $sampleId)
  }
`

export const startSort =
  (api: Api) =>
  async ({ sampleId }: { sampleId: number }): Promise<boolean> => {
    const data = await api.mutation<{
      labStartSort: number // NOTE this is records affected, 1 = success, 0 = failure
    }>(startSortMutation, { sampleId })

    return !!data.labStartSort
  }
