import { ASTNode } from 'graphql'
import { atom, useRecoilValue } from 'recoil'
// import log from 'loglevel'

import { MockApi } from './MockApi'

export interface Api {
  signOut: () => void
  query: <T>(query: ASTNode, variables?: Record<string, unknown>) => Promise<T>
  queryAllPages: <T>(
    query: ASTNode,
    variables: Record<string, unknown>,
    options: { dataProperty: string }
  ) => Promise<T>
  mutation: <T>(query: ASTNode, variables?: Record<string, unknown>) => Promise<T>
}

export const apiState = atom({
  key: 'apiState',
  default: new MockApi() as Api,
  dangerouslyAllowMutability: true,
})

export const useApi = (): Api => useRecoilValue(apiState)
