/** @jsxImportSource @emotion/react */
import React, { useState, useEffect } from 'react'
import { App } from '../App'
import { useHostedAuth, UseHostedReturn } from '@namcbugdb/react-common'
import { appDefaults, endpoints, redirectUrl } from '../config'
import { api } from '@namcbugdb/common'
import CircularProgress from '@mui/material/CircularProgress'
import log from 'loglevel'

const AppAuthConnect: React.FC = () => {
  const [authInfo, setAuthInfo] = useState<Record<string, unknown>>()
  const [isAppPreLoading, setIsAppPreLoading] = useState(true)
  const auth: UseHostedReturn = useHostedAuth(authInfo as Record<string, unknown>, false, false) //config.isDev

  // This is the pre-auth step to retrieve all the necessary cognito endpoint data
  useEffect(() => {
    api
      .authCheck(endpoints.graphql as string, '', appDefaults.apiTimeout)
      .then((authResp) => {
        log.debug('authResp', authResp)
        const authVals = authResp.data?.auth
        if (authVals && authVals.region) {
          setAuthInfo({
            region: authVals.region,
            userPoolId: authVals.userPool,
            userPoolWebClientId: authVals.clientId,
            mandatorySignIn: true,
            oauth: {
              domain: authVals.domain,
              scope: ['email', 'profile', 'openid'],
              redirectSignIn: redirectUrl.toString(),
              redirectSignOut: redirectUrl.toString(),
              responseType: 'code',
            },
          })
          setIsAppPreLoading(false)
        }
      })
      .catch((err) => {
        log.error('api.getPrograms error', err)
      })
  }, [])

  useEffect(() => {
    if (!auth.isAuthenticated && auth.isAuthComplete) auth.signIn()
  }, [auth.isAuthenticated, auth.isAuthComplete])

  if (!auth.isAuthenticated) {
    return (
      <div
        css={{
          height: '100vh',
          width: '100%',
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
          justifyContent: 'center',
        }}
      >
        <CircularProgress />
        {(() => {
          if (isAppPreLoading) return 'Loading...'
          if (!auth.isAuthComplete) return 'Authenticating...'
          return 'Redirecting...'
        })()}
      </div>
    )
  }

  return <App signOut={auth.signOut} auth={auth} />
}
export default AppAuthConnect
