import gql from 'graphql-tag'
import { Profile as GraphProfile, UserPermissions } from '@namcbugdb/common'

import { Api } from '../api'
import { User } from '../../types'

export const fetchMyUserQuery = gql`
  query {
    myProfile {
      id
      firstName
      lastName
      email
      permissions
      affiliationId
      organizationName
    }
  }
`

export const fetchMyUser =
  (api: Api) => async (): Promise<User & { permissions: UserPermissions; affiliationId: number }> => {
    const data = await api.query<{
      myProfile: GraphProfile
    }>(fetchMyUserQuery)
    const { myProfile } = data

    return {
      ...myProfile,
      permissions: myProfile.permissions || {},
      fullName: `${myProfile.firstName} ${myProfile.lastName}`,
    }
  }
