export const selectMyUser = selector<MyUser>({
  key: 'selectors/myUser',
  get: async ({ get }) => {
    const api = get(apiState)
    const myUser = await fetchMyUser(api)()
    const hasPermission = (resource: NAMCResource, grantAction: GrantAction) => {
      const explicitPermission = !!myUser.permissions[grantAction]?.includes(resource)
      const implicitPermission = !!myUser.permissions[grantAction.replace('Own', 'Any')]?.includes(resource) // if I have Any, implicitly have Own
      return explicitPermission || implicitPermission
    }
    return { ...myUser, hasPermission }
  },
})

