import React, { useMemo } from 'react'
import { useRecoilValue } from 'recoil'
import Skeleton from '@mui/material/Skeleton'

import { withSuspense } from '../withSuspense'
import { DataGrid } from '../DataGrid'
import { selectBoxSamplesByBoxId } from '../../data/selectors'
import { samplesColumnDefs } from './columnDefs'
import { BoxSample } from '../../types'
import { formatUtcDateTimeToAppDate } from '../../lib/formats'
import { useNavigate } from '../AppRouter'
import { renderers } from '../DataGrid'

interface BoxDetailProps {
  boxId: number
}

interface BoxSampleRow extends BoxSample {
  sampleId: {
    label: number
    onClick: () => void
  }
  sortState: string
  identificationState: string
}

export const BoxSamples = withSuspense(
  ({ boxId }: BoxDetailProps): JSX.Element => {
    const boxSamples = useRecoilValue(selectBoxSamplesByBoxId(boxId))
    const navigate = useNavigate()

    const rows = useMemo<BoxSampleRow[]>(
      () =>
        boxSamples.map((boxSample) => {
          const { id } = boxSample
          const sampleId = {
            label: id,
            onClick: () => navigate.sampleOverview({ sampleId: id }),
          }

          const { sortEndDate, sortStartDate, sorterName } = boxSample
          const sortState = (() => {
            if (sortEndDate) return `${sorterName} on ${formatUtcDateTimeToAppDate(sortEndDate)}`
            if (!sortStartDate) return 'Not sorted'
            return `Started by ${sorterName} on ${formatUtcDateTimeToAppDate(sortStartDate)}`
          })()

          const { identifierName, identificationEndDate, identificationStartDate } = boxSample
          const identificationState = (() => {
            if (identificationEndDate)
              return `${identifierName} on ${formatUtcDateTimeToAppDate(identificationEndDate)}`
            if (!identificationStartDate) return 'Not identified'
            return `Started by ${identifierName} on ${formatUtcDateTimeToAppDate(identificationStartDate)}`
          })()

          return {
            ...boxSample,
            sampleId,
            sortState,
            identificationState,
          }
        }),
      [boxSamples]
    )

    return <DataGrid<BoxSampleRow> columnDefs={samplesColumnDefs} rows={rows} frameworkComponents={renderers} />
  },
  () => <Skeleton height="3em" variant="rectangular" />
)
