import gql from 'graphql-tag'

import { Api } from '../api'

interface BoxUpdate {
  boxId: number
  boxStateId: number
  measurements: boolean
  midges: boolean
  description: string
  modelIds: number[]
}

export const updateBoxMutation = gql`
  mutation (
    $boxId: Int!
    $boxStateId: Int!
    $customerId: Int!
    $alias: String
    $description: String
    $measurements: Boolean!
    $midges: Boolean!
    $modelIds: [Int]
  ) {
    setBoxInfo(
      boxId: $boxId
      boxStateId: $boxStateId
      customerId: $customerId
      alias: $alias
      description: $description
      measurements: $measurements
      midges: $midges
      models: $modelIds
    )
  }
`

export const updateBox =
  (api: Api) =>
  async (boxUpdate: BoxUpdate): Promise<boolean> => {
    try {
      const data = await api.mutation<{
        setBoxInfo: number // NOTE: count of records changed
      }>(updateBoxMutation, { ...boxUpdate })

      return !!data.setBoxInfo
    } catch (error) {
      return false
    }
  }
